.footer{
  padding: 40px 0;
  background: #F3FAFF;
}

.footer-container{
  position: relative;
  @media(max-width: 991px) {
    flex-direction: column;
    gap: 32px;
  }
}

.footer-main{
  width: 356px;
  background: var(--white);
  border-radius: 20px;
  border: 1px solid var(--gray-200);
  padding: 24px;
  @media(max-width: 991px) {
    width: 100%;
    padding: 20px;
  }
}

.footer-logo{
  display: flex;
}

.footer-contact{
  padding-top: 32px;
  margin: 20px 0 15px;
  border-top: 1px solid var(--gray-200);
  display: flex;
  flex-direction: column;
  gap: 20px
}

.footer-phone{
  display: inline-flex;
  margin-bottom: 4px;
  line-height: 1.2;
}

.footer-email{
  color: var(--blue);
  &:hover{
    color: var(--blue-2);
  }
}

.footer-menu{
  width: 583px;
  display: flex;
  align-items: flex-start;
  gap: 70px;
  @media(max-width: 991px) {
    justify-content: space-between;
  }
  @media(max-width: 767px) {
    width: 100%;

    flex-direction: column;
    gap: 32px;
  }
}

.footer-menu__items{
  display: flex;
  flex-direction: column;
  gap: 12px;
  flex-basis: calc(48% - 35px);
  &:nth-child(2){
    flex-basis: calc(52% - 35px);
  }
  @media(max-width: 991px) {
    flex-basis: auto !important;
  }
}

.footer-menu__head{
  color: #102563;
  @media(max-width: 991px) {
    margin-bottom: 8px !important;
  }
}

.footer-menu__link{
  font-size: 16px;
  font-weight: 500;
  line-height: 1.3;
  display: inline-flex;
  &:hover{
    color: var(--blue);
  }
}

.arrow-top{
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 20px;
  right: 26px;
  background: var(--blue);
  width: 44px;
  height: 44px;
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  opacity: 0;
  transition: all .25s linear;
  z-index: 2;
  .icon{
    stroke: var(--white);
    fill: transparent;
  }
  &:hover{
    background: var(--blue-2);
  }
  &.visible{
    opacity: 1;
    pointer-events: auto;
  }
}

.footer-pay-desktop{
  @media(max-width: 1279px) {
    display: none !important;
  }
}

.footer-button-mobile{
  display: none;
  @media(max-width: 1279px) {
    display: flex;
    margin-top: 20px;
  }
}