.tabs__nav {
  display: flex;
  list-style: none;
  padding: 0;
  gap: 12px;
  flex-flow: row wrap;
  @media(max-width: 991px) {
    gap: 8px;
  }
}

.tabs__tab {
  cursor: pointer;
  border-radius: 4px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #EDEDED;
  width: 200px;
  font-weight: 500;
  font-size: 14px;
  &:hover{
    background: var(--blue);
    color: var(--white);
  }
  @media(max-width: 991px) {
    width: auto;
    padding: 0 18px;
  }
}

.tabs__tab.active {
  background: var(--blue);
  color: var(--white);
  font-weight: 600;
}

.tabs__panel {
  display: none;
  opacity: 0;
}

.tabs__panel.active {
  display: block;
  animation: show-content .25s linear forwards;
}

@keyframes show-content {
  to{
    opacity: 1;
  }
}