.faq-container{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 32px;
  @media(max-width: 991px) {
    flex-direction: column;
    gap: 0;
    display: flex;
  }
}

.accordion__content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.accordion__content-main{
  padding-top: 10px;
}

.accordion__item{
  padding: 20px 0;
  border-bottom: 1px solid var(--gray-200);
  &.open{
    .accordion__head{
      .icon{
        transform: rotate(180deg);
      }
    }
  }
  @media(max-width: 991px) {
    padding: 5px 0;
  }
}
.accordion__head{
  padding: 8px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  text-align: left;
  width: 100%;
  .icon{
    fill: transparent;
    stroke: var(--blue);
    flex-shrink: 0;
    margin-left: 10px;
    transition: all .25s linear;
    width: 24px;
    height: 24px;
  }
  @media(max-width: 991px) {
    font-size: 16px !important;
  }
}