/*    */
.modal {
  display: none; /*    */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /*   */
  z-index: 9999;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

/*   */
.modal-content {
  background-color: var(--blue);
  color: var(--white);
  padding: 60px;
  border-radius: 30px;
  max-width: 634px;
  position: relative;
}

/*   */
.close-modal {
  position: absolute;
  top: 20px;
  right: 20px;
  background: transparent;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all .25s linear;
  border-radius: 50%;
  .icon{
    stroke: var(--white);
  }
  &:hover{
    background: var(--white);
    .icon{
      stroke: var(--blue);
    }
  }
}

/*     */
.modal.show {
  display: flex;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
