  max-width: 634px;
  position: relative;
}

/*   */
.close-modal {
  position: absolute;
  top: 20px;
  right: 20px;
  background: transparent;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.25s linear;
  border-radius: 50%;
}
.close-modal .icon {
  stroke: var(--white);
}
.close-modal:hover {
  background: var(--white);
}
.close-modal:hover .icon {
  stroke: var(--blue);
}

/*     */
.modal.show {
  display: flex;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}