body{
  .header-address__phone{
    &:hover{
      color: var(--blue);
    }
  }

  .social-link{
    border-radius: 50%;
    background: var(--blue);
    &:hover{
      background: var(--blue-2);
    }
  }

  .header-event__button-icon{
    border-radius: 50%;
    transition: all .25s linear;
    background: var(--blue);
    svg{
      fill: var(--white);
    }
  }
  .header-event__button{
    border-radius: 100px;
    &:hover{
      border: 1px solid var(--blue-2);
      .header-event__button-icon{
        background: var(--blue-2);
      }
    }
  }
  .button{
    transition: all .25s linear;
    cursor: pointer;
  }
  .button-white-border{
    background: transparent;
    color: var(--white);
    border: 1px solid var(--white);
    &:hover{
      background: var(--white);
      color: var(--blue);
    }
  }
  .button-blue-border{
    background: transparent;
    color: var(--blue);
    border: 1px solid var(--blue);
    &:hover{
      background: var(--blue);
      color: var(--white);
    }
  }
  .button-white{
    background: var(--white);
    color: var(--blue);
    &:hover{
      background: var(--white-2);
      color: var(--blue);
    }
  }
  .button-red{
    background: var(--red);
    color: var(--white);
    &:hover{
      background: var(--red-2);
      color: var(--white);
    }
  }
  .button-blue{
    background: var(--blue);
    color: var(--white);
    &:hover{
      background: var(--blue-2);
      color: var(--white);
    }
  }
  .header-flex{
    border-radius: 15px;
    background: var(--blue);
  }
  .header-menu__item-drop{
    color: var(--white);
    cursor: pointer;
    .icon{
      stroke: var(--white);
    }
  }
  .header-menu__item-link{
    color: var(--white);
    &:hover{
      color: var(--white-2);
    }
  }
  .start-slide{
    border-radius: 30px;
    overflow: hidden;
    position: relative;
    &:before{
      content: '';
      background: linear-gradient(to right, rgba(16, 37, 99, 0.7), transparent);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    @media(max-width: 991px) {
      border-radius: 20px;
    }
  }
  .cause-items{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;
    @media(max-width: 991px) {
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
  }
  .cause-item{
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 25px;
    background: var(--light-blue);
    border: 2px solid var(--gray-200);
    border-radius: 15px;
    @media(max-width: 991px) {
      padding: 20px;
    }
  }
  .cause-item_blue{
    color: var(--white);
    background: var(--blue);
    border: 2px solid var(--blue);
  }
  .popular-items{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
    @media(max-width: 1199px) {
      flex-direction: column;
      display: flex;
    }
    @media(max-width: 991px) {
      gap: 20px;
    }
  }
  .popular-item{
    height: 535px;
    padding: 24px;
    display: flex;
    align-items: flex-end;
    border-radius: 20px;
    background-size: cover;
    @media(max-width: 991px) {
      height: 450px;
      padding: 20px;
    }
  }
  .popular-item__content{
    background: var(--white);
    padding: 20px;
    border-radius: 10px;
  }
  .bg-gray{
    background: #EDEDED;
  }
  .send-section{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: var(--white);
    border-radius: 30px;
    padding: 20px 60px;
    height: 168px;
    .icon{
      flex-shrink: 0;
      width: 44px;
      height: 44px;
      margin-left: 10px;
      fill: transparent;
      stroke: var(--blue);
      @media(max-width: 991px) {
        width: 32px;
        height: 32px;
        transform: rotate(90deg);
      }
    }
    &:hover{
      background: var(--white-2);
    }
    @media(max-width: 991px) {
      padding: 20px 24px;
    }
  }
  .section-special{
    padding: 60px 40px;
    background: var(--blue);
    border-radius: 30px;
    @media(max-width: 991px) {
      padding: 40px 26px;
      margin: 0 -26px -40px;
      border-radius: 20px;
    }
  }
  .special-items{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
    @media(max-width: 991px) {
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
  }
  .special-item{
    background: var(--white);
    padding: 25px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 216px;
    color: var(--blue);
    &:hover{
      color: var(--white);
      background: var(--red);
      .special-item__icon{
        background: var(--white);
        svg{
          fill: var(--red);
        }
      }
    }
    @media(max-width: 991px) {
      height: 175px;
      padding: 20px;
    }
  }
  .special-item__icon{
    transition: all .25s linear;
    width: 44px;
    height: 44px;
    background: var(--blue);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg{
      fill: var(--white);
    }
  }
  .cruise-catalog{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;
    @media(max-width: 1199px) {
      display: flex;
      flex-direction: column;
    }
    @media(max-width: 991px) {
      gap: 20px;
    }
  }
  .cruise-item{
    border-radius: 20px;
    display: flex;
    align-items: center;
    padding: 24px;
    height: 368px;
    background-size: cover;
    @media(max-width: 991px) {
      padding: 20px;
      height: 450px;
      align-items: flex-end;
    }
  }
  .cruise-item__content{
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    width: 300px;
    background: var(--white);
    border-radius: 10px;
    padding: 20px;
    @media(max-width: 991px) {
      height: auto;
      width: 100%;
      p{
        margin-bottom: 20px;
      }
    }
  }
  .cruise-item__link, .link-arrow{
    font-weight: 500;
    display: flex;
    align-items: center;
    color: var(--blue);
    .icon{
      fill: transparent;
      stroke: var(--blue);
      margin-left: 8px;
    }
    &:hover{
      color: var(--blue-2);
      .icon{
        stroke: var(--blue-2);
      }
    }
  }
  .read-block{
    background-size: cover;
    height: 332px;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 40px;
    p{
      max-width: 581px;
    }
    @media(max-width: 991px) {
      margin: -40px -26px;
      padding: 40px 26px;
      border-radius: 20px;
      height: 572px;
      background-position: center;
      justify-content: flex-start;
    }
  }
  .news-arrow{
    display: flex;
    align-items: flex-end;
    flex: 1;
  }
  .news-items{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
    @media(max-width: 991px) {
      flex-direction: column;
      gap: 20px;
      display: flex;
    }
  }
  .news-item{
    display: flex;
    flex-direction: column;
    &:hover{
      .news-item__pic{
        img{
          transform: scale(1.1);
        }
      }
    }
  }
  .news-item__pic{
    height: 220px;
    overflow: hidden;
    border-radius: 20px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all .25s linear;
    }
  }
  .review-items{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;
    @media(max-width: 991px) {
      flex-direction: column;
      gap: 20px;
      display: flex;
    }
  }
  .review-item{
    background: var(--white);
    padding: 25px;
    border-radius: 15px;
    @media(max-width: 991px) {
      padding: 20px;
    }
  }
  .review-item__head{
    @media(max-width: 991px) {
      flex-direction: column-reverse;
      align-items: flex-start;
    }
  }
  .review-item__user{
    display: flex;
    align-items: center;
    @media(max-width: 991px) {
      margin-top: 12px;
    }
  }
  .review-item__user-avatar{
    width: 44px;
    height: 44px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    margin-right: 12px;
    overflow: hidden;
  }
  .review-item__tag{
    color: var(--blue);
    height: 32px;
    display: flex;
    align-items: center;
    border-radius: 30px;
    padding: 0 15px;
    border: 1px solid var(--gray-200);
  }
  .question-container{
    background: var(--blue);
    border-radius: 30px;
    padding: 60px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;
    position: relative;
    @media(max-width: 991px) {
      display: flex;
      flex-direction: column;
      gap: 32px;
      margin: -40px -26px 0;
      padding: 40px 26px;
      border-radius: 20px;
    }
  }
  .form-pic{
    position: absolute;
    left: 60px;
    bottom: 60px;

    display: flex;
    overflow: hidden;
    img{
      border-radius: 20px;
    }
    @media(max-width: 991px) {
      position: relative;
      left: auto;
      bottom: auto;
    }
  }
  .dop-menu{
    display: block !important;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    width: 100%;
    left: 0;
    top: 170px;
    background: var(--white);
    padding: 40px 0;
    z-index: 5;
    transition: all .25s linear;
    font-size: 16px;
    &:before{
      content: '';
      top: -5px;
      height: 5px;
      width: 100%;
      background: transparent;
      left: 0;
      position: absolute;
      @media(max-width: 1279px) {
        display: none;
      }
    }
    @media(max-width: 1279px) {
      height: 0;
      overflow: hidden;
      transition: all .25s linear;
      opacity: 1;
      background: transparent;
      position: relative;
      top: auto;
      left: auto;
      pointer-events: auto;
      padding: 0;
      a{
        color: var(--white);
        .icon{
          stroke: var(--white);
        }
      }
      .container{
        padding: 0;
      }
    }
  }
  .dop-menu__container{
    display: flex;
    align-items: flex-start;
    gap: 32px;
    @media(max-width: 1279px) {
      flex-direction: column;
      display: flex;
      gap: 20px;
      padding-bottom: 12px;
    }
  }
  .dop-list{
    display: flex;
    flex-direction: column;
    gap: 9px;
    font-weight: 500;
    margin-bottom: 20px;
    &:last-child{
      margin-bottom: 0;
    }
    a{
      &:hover{
        color: var(--blue);
      }
    }
  }
  .dop-menu__left{
    flex-basis: 280px;
    flex-shrink: 0;
    @media(max-width: 1279px) {
      flex-basis: auto;
    }
  }
  .dop-menu__right{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    @media(max-width: 1279px) {
      display: flex;
      flex-direction: column;
    }
  }
  .header-menu__item_dropdown{
    &.active{
      .header-menu__item-drop{
        .icon{
          transform: rotate(180deg);
        }
      }
      .dop-menu{
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .header-menu__item-drop{
    .icon{
      transition: all .25s linear;
    }
  }
  .header-main{
    @media(max-width: 1279px) {
      display: block !important;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      opacity: 0;
      pointer-events: none;
      overflow: auto;
      z-index: 10;
      transition: all .25s linear;
      background: var(--blue);
      border-radius: 0;
      padding: 32px 0;
    }
  }
  .header-menu__items{
    @media(max-width: 1279px) {
      flex-direction: column;
      width: 100%;
      gap: 0;
    }
  }
  .header-menu__item-drop{
    @media(max-width: 1279px) {
      padding: 12px 0;
    }
  }
  .header-flex{
    @media(max-width: 1279px) {
      flex-direction: column;
      padding: 0;
      align-items: flex-start;
    }
  }
  .social_mobile{
    gap: 10px !important;
  }
  .header-menu{
    @media(max-width: 1279px) {
      width: 100%;
    }
  }
  .header-menu__item{
    @media(max-width: 1279px) {
      width: 100%;
      border-bottom: 1px solid var(--white);
      &:last-child{
        border-bottom: none;
      }
    }
  }
  .header-menu__item-link{
    @media(max-width: 1279px) {
      padding: 12px 0;
      display: flex;
    }
  }
  .header-button{
    @media(max-width: 1279px) {
      display: none;
    }
  }
  .close-mobile-menu{
    @media(max-width: 1280px) {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 18px;
      right: 20px;
      .icon{
        stroke: var(--white);
      }
    }
  }
  .menu-more{
    display: flex;
    flex-direction: column;
    margin-top: 16px;
    width: 100%;
  }
  .menu-more__link{
    font-size: 16px;
    color: var(--white);
    font-weight: 500;
    display: flex;
    padding: 16px 0;
  }
  .menu-more__item{
    border-bottom: 1px solid var(--white);
    &:last-child{
      border-bottom: none;
    }
  }
  .contact-mobile{
    margin: 24px 0 15px;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  .header-address-phone{
    font-size: 16px;
    font-weight: 600;
    color: var(--white);
    margin-bottom: 4px;
  }
  .header-address-value{
    color: var(--gray-200);
    font-weight: 500;
  }
  .header-mail{
    font-weight: 600;
    color: var(--white);
  }
}
.menu-show{
  overflow: hidden;
  .blackout{
    opacity: 1;
    pointer-events: auto;
  }
}

.menu-active{
  .header-main{
    opacity: 1 !important;
    pointer-events: auto !important;
  }
}